:root {
  --pine-green: #077675;
  --pear: #cce335;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* view source link */
.github-text {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  fill: #fff;
  font-size: 50px;
}
.github-corner {
  fill:#fff;
}
.github-corner:focus,
.github-corner:hover  {
  fill: #cce335;
  fill: var(--pear);
}

.github-corner:focus .github-text,
.github-corner:hover .github-text {
  fill: #cce335;
  fill: var(--pear);
}
.octocat{
  color:#282c34; 
  position: absolute; 
  top: 0; 
  border: 0; 
  right: 0;
}
.github-corner:hover .octo-arm{-webkit-animation:octocat-wave 560ms ease-in-out;animation:octocat-wave 560ms ease-in-out}
@-webkit-keyframes octocat-wave{
  0%,100%{-webkit-transform:rotate(0);transform:rotate(0)}20%,60%{-webkit-transform:rotate(-25deg);transform:rotate(-25deg)}40%,80%{-webkit-transform:rotate(10deg);transform:rotate(10deg)}
}
@keyframes octocat-wave{
  0%,100%{-webkit-transform:rotate(0);transform:rotate(0)}20%,60%{-webkit-transform:rotate(-25deg);transform:rotate(-25deg)}40%,80%{-webkit-transform:rotate(10deg);transform:rotate(10deg)}
}
  
@media (max-width:500px){
  .github-corner:hover .octo-arm{-webkit-animation:none;animation:none}
  .github-corner .octo-arm{-webkit-animation:octocat-wave 560ms ease-in-out;animation:octocat-wave 560ms ease-in-out}
}


[aria-label=Forms] {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;

}
.App-header {
  background-color: #282c34;

  display: -webkit-flex;

  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

body {
  background-color: #282c34;
}
.form-wrapper {
  box-sizing: border-box;
  background-color: #f8f8f8;
  color: black;
  max-width: 50%;
  min-width: 26rem;
  margin: 0 auto;
  font-size: 1.25rem;
  padding: 2rem;
  margin: 0.25rem;
}

.form-wrapper fieldset {
  padding: 2rem 1rem;
}

.form-field-wrapper {
  padding: 0.5rem 1rem 1.25rem 0.75rem;
}

.form-field-wrapper .field-status {
  display: none;
}
.form-field-wrapper.problem .field-status {
  display: inherit;
}
.form-field-wrapper.problem {
  border: 1px solid #ff3387;
  
  background-color: rgba(255,145,145,0.5);
}

.form-field-wrapper.problem .field-status {
  color:  #ff3387;
}


.form-wrapper input[type=text] {
  font-size: 1.5rem;
  height: 2.75rem;
  max-width: 100%;
  width: 100%;
}

legend {
  background-color: #4a4a4a;
  color: white;
  padding: 0.5rem 1.25rem;
  position: relative;
  margin: -0.25rem;
  border-top-left-radius: 0.75rem;
  border-top-right-radius: 0.75rem;
}

.button {
  background-color: #4a4a4a;
  color: white;
  border-radius: 1.5rem;
  padding: 0.5rem 1rem;
  font-size: 1.5rem;
}

.debug {
  font-size: 11px;
  border-radius: 0.5rem;
  font-weight: 500;
  padding: .5rem;
  background: #555;
  color: #fff;
  letter-spacing: 1px;
}

.loading {
  fill: #282c34;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.loading-status {
  padding-left: 1rem;
}
